<template>
  <div class="zoehis_menu zoehis_menu_font zoehis_menu_css"
    :label="label" ref="zoehis_menu_key"
    :class="{'zoehis_check1':isActivity && !isType2 && !isType3,
      'zoehis_check2': isActivity && isType2,
      'zoehis_menu_type2' : isType2,
      'zoehis_menu_style2' : isType2,
      'zoehis_menu_hover' : isHover && !isType3,
      'zoehis_check3': isActivity && isType3,
      'zoehis_menu_type3' : isType3,
      'zoehis_menu_style3' : isType3,
      'zoehis_menu_style3_hover' : isHover && isType3,
      'zoehis_menu_visible' : _radioGroup.hasReplaceMenu && isHidden,
    }"
    @click="handleClick(0)"
    v-model="model"
  >
    <slot></slot>
  </div>
</template>

<script type="text/ecmascript-6">
  import Emitter from '../../../src/mixins/emitter.js'
  export default{
    name: "ZoehisMenu",
    componentName: "ZoehisMenu",
    mixins: [Emitter],

    data(){
      return {
        isHidden:false,//是否显示该menu
      }
    },

    props: {
      label: {},
      disabled: {
        type: Boolean,
        default: false
      },
    },

    computed: {
//      查找父组件ZoehisMenuGroup，并赋值给_radioGroup
      isGroup(){
        let parent = this.$parent;
        while (parent) {
          if (parent.$options.name !== 'ZoehisMenuGroup') {
            parent = parent.$parent;
          } else {
            this._radioGroup = parent;
            return true;
          }
        }
        return false;
      },

//      是否选中
      isActivity(){
        if(this.isGroup){
          return this.label === this.model
        }
      },

//      风格2
      isType2(){
        if(this.isGroup){
          return this._radioGroup.$props.type === "style2"
        }
      },

//      风格3
      isType3(){
        if(this.isGroup){
          return this._radioGroup.$props.type === "style3"
        }
      },

//      是否hover
      isHover(){
        if(this.isGroup){
          return this._radioGroup.$props.hover
        }
      },

      model: {
        get(){
          return  this._radioGroup.value;
        },

        set(val) {
          if (this.isGroup) {
            this.dispatch('ZoehisMenuGroup', 'input', [val]);
          }
        }
      },

      isDisabled(){
        if(this.isGroup){
          return this._radioGroup.$props.disabled;
        }
      },
      replaceMenuList(){
        if(this.isGroup && this._radioGroup.hasReplaceMenu){
          return this._radioGroup.$data.replaceMenuList;
        }
      }
    },

    created(){
    },

    methods: {
//      控制当前菜单是否显示
      handleShowState(boolean){
        this.isHidden = boolean;
      },
      handleClick(type=0){// 默认点击为0 传1表示下拉框切换调用
        var beforeClick = this._radioGroup.$props.beforeClick;
        if(typeof beforeClick == "function"){
          beforeClick(()=>{
            this.continue(type);
          },this.label,this.model);
        }else {
          this.continue(type);
        }
      },
      continue(type){// 默认点击为0 传1表示下拉框切换调用
        if(this.isDisabled || this.disabled) return false;  //menu禁用
        let oldLabel = this.model;
        this.model = this.label;  //切换menu
        if(this._radioGroup.hasReplaceMenu){
          if(type==0){
            this._radioGroup.setReplaceMenu('');
          }else if(type == 1){
            this._radioGroup.selectReplaceMenu(this.label);
          }
        }
        this.$emit('menuClick',this.label,oldLabel);  //触发父组件事件
      },

    },
    watch:{
      'replaceMenuList.length'(num,odlnum){
        if(this.replaceMenuList[0] && this.label == this.replaceMenuList[0][this._radioGroup.$props.itemcode]){
          this.handleShowState(true);
        }else{
          this.handleShowState(false);
        }
      }
    }

  }
</script>



