<template>
  <div class="zoehis_time_spinner" :class="{'has_seconds': showSeconds }">
    <div
      @scroll="handleScrollEve('hour')"
      class="zoehis_time_spinner__wrapper common_scroll_webkit"
      ref="hour_zoehis">
      <div class="zoehis_time_spinner__list">
        <li
          @click="handleClick('hours',{ value: hour, disabled: disabled }, true)"
          v-for="(disabled,hour) in hoursList"
          track-by="hour"
          class="zoehis_time_spinner__item"
          :class="{ 'active': hour === hours, 'disabled': disabled }"
        >{{hour|$filterZero}}</li>
      </div>

    </div>
    <div
      @scroll="handleScrollEve('minute')"
      class="zoehis_time_spinner__wrapper common_scroll_webkit"
      ref="minute_zoehis">
      <div class="zoehis_time_spinner__list">
        <li
          @click="handleClick('minutes',key)"
          v-for="(minute,key) in 60"
          class="zoehis_time_spinner__item"
          :class="{ 'active': key === minutes }"
        >{{key | $filterZero}}</li>
      </div>
    </div>
    <div
      @scroll="handleScrollEve('second')"
      class="zoehis_time_spinner__wrapper common_scroll_webkit"
      v-show="showSeconds"
      ref="second_zoehis">
      <div class="zoehis_time_spinner__list">
        <li
          @click="handleClick('seconds',key)"
          v-for="(second,key) in 60"
          class="zoehis_time_spinner__item"
          :class="{ 'active': key === seconds }"
        >{{key | $filterZero}}</li>
      </div>
    </div>
  </div>
</template>
<script type="text/babel">
  import ZoehisScrollbar from '../../../scrollbar'
  import {getRangeHours} from '../util'
  import debounce from '../../../../src/utils/throttle-debounce'
  export default {
    components: {ZoehisScrollbar},
    watch:{
      timeIsChange(obj){
        this.emitChange(obj);
      },
      hoursPrivate(newVal,oldVal){//监听hoursPrivate的值，用于点击"时"的事件
        if(!(newVal>=0 && newVal<=23)){
          this.hoursPrivate = oldVal;
        }
        this.ajustZoehisTop('hour',newVal);
      },
      minutesPrivate(newVal,oldVal){//监听minutesPrivate的值，用于点击"分"的事件
        if(!(newVal>=0 && newVal<=59)){
          this.minutesPrivate = oldVal;
        }
        this.ajustZoehisTop('minute',newVal);
      },
      secondsPrivate(newVal,oldVal){//监听secondsPrivate的值，用于点击"秒"的事件
        if(!(newVal>=0 && newVal<=59)){
          this.secondsPrivate = oldVal;
        }
        this.ajustZoehisTop('second',newVal);
      },
    },
    props: {
      showSeconds: { //是否显示秒
        type: Boolean,
        default: true
      },
      hours: {//当前选中的小时
        type: Number,
        default: 0
      },
      minutes: { //当前选中的分
        type: Number,
        default: 0
      },
      seconds: {  //当前选中的秒
        type: Number,
        default: 0
      }
    },
    data(){
      return {
        selectableRange:[],
        hoursPrivate: 0,
        minutesPrivate: 0,
        secondsPrivate: 0
      }
    },
    filters:{
      //显示补0
      $filterZero(val){
        if(val<10){
          return "0"+val;
        }else {
          return val;
        }
      }
    },
    created(){
      this.debounceAjustZoehisTop = debounce(100, type => this.ajustZoehisTop(type, this[`${type}s`]));
    },
    computed: {
      timeIsChange(){
        return {
          hours:this.hoursPrivate,
          minutes:this.minutesPrivate,
          seconds:this.secondsPrivate
        };
      },
      //获取小时列表
      hoursList() {
        return getRangeHours(this.selectableRange);
      },
    },
    mounted(){

    },
    methods: {
      emitChange(obj){
        this.$emit('change', obj);
      },
      handleScrollEve(type){
        if(this[`${type}Flag`]){
          this[`${type}Flag`]=false;
          return;
        }
        const ajust = {};
        const item = Math.floor((event.srcElement.scrollTop - 80) / 32 + 3);
        ajust[`${type}s`] = Math.min(item,type==='hour' ? 23 : 59);
        this[`${type}sPrivate`]=ajust[`${type}s`];
        this.debounceAjustZoehisTop(type);
        this.emitChange(ajust);//emit=》chang,参数为滚动条选中的值
      },
      //点击事件
      handleClick(type, value){
        if(value.disabled) return;

        this[`${type}Private`] = value.value>=0 ? value.value : value;
        this.emitSelectRange(type)
      },
      //全选输入框的某一段
      emitSelectRange(type){
        if(type === 'hours'){
          this.$emit('select-range',0,2);
        }else if(type === 'minutes'){
          this.$emit('select-range',3,5);
        }else if(type === 'seconds'){
          this.$emit('select-range',6,8);
        }
      },
      //重置选中值的滚动条位置
      ajustScrollTop(){
        this.hourFlag = true;//用于阻止过滤滚动条事件handleScrollEve再次触发
        this.minuteFlag = true;
        this.secondFlag = true;
        this.hoursPrivate=this.hours;
        this.minutesPrivate=this.minutes;
        this.secondsPrivate=this.seconds;
      },
      //初始化滚动条的位置
      initScrollPos(){
        this.ajustZoehisTop('hour', this.hours);
        this.ajustZoehisTop('minute', this.minutes);
        this.ajustZoehisTop('second', this.seconds);
      },
      //将滚动条滚动到选中值的位置上
      ajustZoehisTop(type, value){
        this.$refs[`${type}_zoehis`].scrollTop = Math.max(0, (value - 2.5) * 32 + 80);
      }
    },

  }
</script>


